<template>
  <div class="bg-white dark:bg-neutral-800 rounded-xl shadow-custom p-6">
    <h2 class="text-lg font-semibold mb-4 flex items-center text-primary dark:text-primary-400">
      <i class="fa fa-upload mr-2"></i>上传图标
    </h2>
    <div
      :class="['border-2 border-dashed border-neutral-200 dark:border-primary-800 rounded-lg p-8 text-center cursor-pointer hover:border-primary transition-all-300', { 'file-drop-active': fileDropActive }]"
      @dragover.prevent="fileDropActive = true"
      @dragleave.prevent="fileDropActive = false"
      @drop.prevent="onDrop"
    >
      <i class="fa fa-cloud-upload text-4xl text-neutral-400 mb-3"></i>
      <p class="text-neutral-600 dark:text-neutral-200 mb-2">拖放图标文件到这里，或</p>
      <label for="fileInput" class="btn-primary px-4 py-2 rounded-lg inline-block cursor-pointer">
        <i class="fa fa-plus mr-1"></i>选择文件
      </label>
      <input id="fileInput" ref="fileInput" type="file" accept="image/*" multiple class="hidden" @change="onFileChange">
      <p class="text-xs text-neutral-500 dark:text-neutral-400 mt-3">支持 PNG, JPG, SVG 格式</p>
    </div>
    <div v-show="files.length" class="mt-4">
      <h3 class="text-md font-medium mb-2 flex items-center text-secondary dark:text-secondary-400">
        <i class="fa fa-files-o mr-2"></i>已上传文件
      </h3>
      <div class="space-y-2 max-h-60 overflow-y-auto pr-1">
        <div v-for="(file, idx) in files" :key="file.name" class="flex flex-col p-2 border border-neutral-200 dark:border-primary-800 rounded-lg bg-neutral-50 dark:bg-neutral-900 hover:bg-neutral-100 dark:hover:bg-neutral-800 transition-all-300">
          <div class="flex items-center justify-between">
            <div class="flex items-center">
              <i class="fa fa-file-image-o text-primary mr-2"></i>
              <span class="text-sm font-medium text-neutral-800 dark:text-neutral-100">{{ file.name }}</span>
              <span class="text-xs text-neutral-500 dark:text-neutral-400 ml-2">{{ formatFileSize(file.size) }}</span>
            </div>
            <button class="text-neutral-400 hover:text-red-500 transition-all-300" @click="$emit('remove-file', idx)">
              <i class="fa fa-times"></i>
            </button>
          </div>
          <div class="mt-2 flex items-center">
            <span class="text-xs text-neutral-500 dark:text-neutral-400 mr-2">CSS类名:</span>
            <input
              type="text"
              class="text-xs px-2 py-1 border border-neutral-200 dark:border-primary-800 rounded flex-grow focus:outline-none focus:ring-1 focus:ring-primary bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100"
              v-model="localCustomNames[file.name]"
              :placeholder="defaultClassName(file.name)"
              @input="emitCustomNames"
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    files: { type: Array, default: () => [] },
    customNames: { type: Object, default: () => ({}) }
  },
  data() {
    return {
      fileDropActive: false,
      localCustomNames: {}
    };
  },
  watch: {
    customNames: {
      handler(val) {
        this.localCustomNames = { ...val };
      },
      immediate: true
    }
  },
  methods: {
    onFileChange(e) {
      this.handleFiles(e.target.files);
      this.$refs.fileInput.value = '';
    },
    onDrop(e) {
      this.fileDropActive = false;
      this.handleFiles(e.dataTransfer.files);
    },
    handleFiles(files) {
      const imageFiles = Array.from(files).filter(f => f.type.startsWith('image/'));
      if (!imageFiles.length) return;
      let newFiles = [...this.files];
      let newNames = { ...this.localCustomNames };
      imageFiles.forEach(file => {
        if (!newFiles.some(f => f.name === file.name)) {
          newFiles.push(file);
        } else {
          const newName = `${file.name.replace(/\.[^/.]+$/, '')}_${Date.now()}${file.name.match(/\.[^/.]+$/)[0]}`;
          const renamedFile = new File([file], newName, { type: file.type });
          newFiles.push(renamedFile);
        }
      });
      this.$emit('files-change', newFiles);
      this.$emit('custom-names-change', newNames);
    },
    emitCustomNames() {
      this.$emit('custom-names-change', { ...this.localCustomNames });
    },
    defaultClassName(name) {
      return name.replace(/\.[^/.]+$/, '').replace(/[^a-zA-Z0-9]/g, '-').replace(/^-+|-+$/g, '') || 'icon';
    },
    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes';
      const k = 1024, sizes = ['Bytes', 'KB', 'MB', 'GB'];
      const i = Math.floor(Math.log(bytes) / Math.log(k));
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
    }
  }
};
</script>
