<template>
  <main class="flex-grow container mx-auto px-4 py-8 bg-primary-50 dark:bg-neutral-900 rounded-2xl">
    <div class="mb-8 flex items-center">
      <i class="fa fa-th-large text-3xl md:text-4xl text-primary dark:text-primary-400 mr-3"></i>
      <div>
        <h1 class="text-2xl md:text-3xl font-bold text-primary dark:text-primary-400 mb-1">雪碧图生成器</h1>
        <p class="text-neutral-600 dark:text-neutral-300 text-base">本地合成多图为雪碧图，自动生成 <span class="text-primary font-semibold">CSS/LESS/SCSS</span> 代码，支持多种排列方式和尺寸调整。</p>
      </div>
    </div>
    <div class="grid grid-cols-1 lg:grid-cols-12 gap-8">
      <div class="lg:col-span-4 space-y-6">
        <FileUpload
          :files="files"
          :customNames="customNames"
          @files-change="onFilesChange"
          @custom-names-change="onCustomNamesChange"
          @remove-file="onRemoveFile"
        />
        <SpriteSettings v-model="settings"/>
      </div>
      <div class="lg:col-span-8 space-y-6">
        <SpritePreview
          :files="files"
          :customNames="customNames"
          :settings="settings"
          @sprite-generated="onSpriteGenerated"
          ref="preview"
          @show-help="showHelp = true"
        />
        <CssCode
          :css="css"
          :format="settings.cssFormat"
          @download="downloadCss"
          @copy="copyCss"
        />
      </div>
    </div>
    <Toast v-if="toast.show" :msg="toast.msg" :type="toast.type"/>
    <HelpDialog v-if="showHelp" @close="showHelp = false"/>
  </main>
</template>
<script>
import FileUpload from '@/components/FileUpload.vue';
import SpriteSettings from '@/components/SpriteSettings.vue';
import SpritePreview from '@/components/SpritePreview.vue';
import CssCode from '@/components/CssCode.vue';
import Toast from '@/components/Toast.vue';
import HelpDialog from '@/components/HelpDialog.vue';

export default {
  components: { FileUpload, SpriteSettings, SpritePreview, CssCode, Toast, HelpDialog },
  data() {
    return {
      files: [],
      customNames: {},
      settings: {
        layoutType: 'binary-tree',
        spacing: 5,
        resizeMode: 'none',
        fixedWidth: 32,
        fixedHeight: 32,
        maxWidth: 64,
        maxHeight: 64,
        scalePercentage: 100,
        outputFormat: 'png',
        quality: 0.9,
        cssFormat: 'css',
        enableDebug: false
      },
      css: '',
      toast: { show: false, msg: '', type: 'success' },
      showHelp: false
    };
  },
  methods: {
    onFilesChange(files) {
      this.files = files;
    },
    onCustomNamesChange(names) {
      this.customNames = names;
    },
    onRemoveFile(idx) {
      this.files.splice(idx, 1);
    },
    onSpriteGenerated({ css }) {
      this.css = css;
    },
    downloadCss() {
      if (!this.css) return;
      const blob = new Blob([this.css], { type: 'text/css' });
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = `sprite.${this.settings.cssFormat}`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      this.showToast('CSS文件下载成功', 'success');
    },
    copyCss() {
      if (!this.css) return;
      navigator.clipboard.writeText(this.css).then(() => {
        this.showToast('CSS代码已复制到剪贴板', 'success');
      }).catch(() => {
        this.showToast('复制失败，请手动复制', 'error');
      });
    },
    showToast(msg, type = 'success') {
      this.toast = { show: true, msg, type };
      setTimeout(() => { this.toast.show = false; }, 3000);
    }
  }
};
</script>
