<template>
  <div class="fixed bottom-8 right-8 px-6 py-4 rounded-xl shadow-lg z-50 flex items-center"
    :class="type==='success' ? 'toast-success' : 'toast-error'">
    <i :class="type==='success' ? 'fa fa-check-circle mr-3' : 'fa fa-exclamation-circle mr-3'"></i>
    <span class="text-base font-medium">{{ msg }}</span>
  </div>
</template>
<script>
export default {
  props: {
    msg: String,
    type: { type: String, default: 'success' }
  }
};
</script>
