<template>
  <div :class="{'dark': isDark}" class="bg-bg-light dark:bg-neutral-900 font-inter text-neutral-800 dark:text-neutral-100 min-h-screen flex flex-col transition-colors duration-300">
    <header class="bg-white dark:bg-neutral-800 shadow-sm sticky top-0 z-50 rounded-b-xl transition-colors duration-300">
      <div class="container mx-auto px-4 py-4 flex justify-between items-center">
        <div class="flex items-center space-x-2">
          <i class="fa fa-toolbox text-primary text-2xl"></i>
          <router-link to="/" class="text-2xl font-bold text-primary hover:text-secondary tracking-tight">创新工具箱</router-link>
        </div>
        <nav class="flex items-center">
          <router-link
            v-if="currentNavTitle"
            :to="$route.path"
            class="ml-4 text-base transition-colors text-primary font-bold cursor-default pointer-events-none"
          >
            {{ currentNavTitle }}
          </router-link>
          <!-- 主题切换按钮 -->
          <button
            class="ml-4 p-2 rounded-full bg-neutral-100 dark:bg-neutral-700 hover:bg-primary/10 dark:hover:bg-primary/20 transition-all-300 text-xl"
            @click="toggleTheme"
            :aria-label="isDark ? '切换为亮色模式' : '切换为暗色模式'"
          >
            <i v-if="isDark" class="fa fa-sun text-yellow-400"></i>
            <i v-else class="fa fa-moon text-primary"></i>
          </button>
        </nav>
      </div>
    </header>
    <div class="flex-1 flex flex-col">
      <router-view />
    </div>
    <AppFooter />
  </div>
</template>
<script>
import AppFooter from '@/components/AppFooter.vue'
export default {
  components: { AppFooter },
  data() {
    return {
      isDark: false
    };
  },
  computed: {
    currentNavTitle() {
      // 只在工具页显示卡片名，首页和帮助页不显示
      const exclude = ['/', '/help'];
      const path = this.$route.path;
      if (exclude.includes(path)) return '';
      return this.$route.meta && this.$route.meta.title ? this.$route.meta.title : '';
    }
  },
  created() {
    // 初始化主题
    const theme = localStorage.getItem('theme');
    if (theme === 'dark' || (!theme && window.matchMedia('(prefers-color-scheme: dark)').matches)) {
      this.isDark = true;
      document.documentElement.classList.add('dark');
    } else {
      this.isDark = false;
      document.documentElement.classList.remove('dark');
    }
  },
  watch: {
    isDark(val) {
      if (val) {
        document.documentElement.classList.add('dark');
        localStorage.setItem('theme', 'dark');
      } else {
        document.documentElement.classList.remove('dark');
        localStorage.setItem('theme', 'light');
      }
    }
  },
  methods: {
    toggleTheme() {
      this.isDark = !this.isDark;
    }
  }
};
</script>
