<template>
  <main class="container mx-auto px-4 py-12 flex-1">
    <!-- 页面标题区 -->
    <div class="mb-16 text-center">
      <h1 class="text-3xl md:text-5xl font-bold text-primary dark:text-yellow-400 mb-4 tracking-tight relative inline-block">
        创新工具箱
        <span class="absolute -top-2 -right-10 text-xs px-2 py-1 bg-red-500 text-white rounded-full transform rotate-12">Beta</span>
      </h1>
      <p class="text-neutral-600 dark:text-neutral-300 max-w-2xl mx-auto">
        为开发者和设计师提供的高效在线工具集，让您的工作流程更加流畅
      </p>
    </div>
    
    <!-- 工具分类标签 -->
    <div class="flex justify-center mb-10 flex-wrap gap-2">
      <button 
        v-for="category in categories" 
        :key="category"
        @click="setCategory(category)"
        class="px-4 py-2 rounded-full font-medium transition-all duration-200"
        :class="getCategoryClasses(category)"
      >
        {{ category }}
        <span class="ml-1 opacity-60">
          ({{ category === CATEGORIES.ALL ? tools.length : getCategoryCount(category) }})
        </span>
      </button>
    </div>
    
    <!-- 搜索栏 -->
    <div class="relative max-w-md mx-auto mb-12">
      <input 
        type="text" 
        placeholder="搜索工具..." 
        class="w-full py-3 px-5 pr-12 rounded-full border-2 border-neutral-200 dark:border-neutral-700 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 focus:outline-none focus:border-primary dark:focus:border-primary-400 transition"
        v-model="searchQuery"
      />
      <i class="fa fa-search absolute right-4 top-1/2 transform -translate-y-1/2 text-neutral-400"></i>
    </div>

    <!-- 工具卡片网格 -->
    <div v-if="filteredTools.length > 0" class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 justify-items-center">
      <ToolCard
        v-for="tool in filteredTools"
        :key="tool.route"
        :tool="tool"
        class="transform transition-all duration-300 hover:scale-105 w-full max-w-[320px]"
      />
    </div>
    
    <!-- 无结果提示 -->
    <div v-else class="text-center py-16">
      <i class="fa fa-search text-4xl text-neutral-300 dark:text-neutral-600 mb-4"></i>
      <p class="text-neutral-500 dark:text-neutral-400 text-lg">没有找到匹配的工具</p>
      <button 
        @click="resetFilters" 
        class="mt-4 px-4 py-2 bg-primary bg-opacity-10 text-primary dark:bg-primary-800 dark:bg-opacity-20 dark:text-primary-300 rounded-full hover:bg-opacity-20"
      >
        重置筛选
      </button>
    </div>

    <div class="mt-20 text-center">
      <p class="text-neutral-500 dark:text-neutral-400 mb-3">更多工具持续开发中，敬请期待！</p>
      <button class="px-6 py-2 rounded-full border border-dashed border-neutral-300 dark:border-neutral-600 text-neutral-500 dark:text-neutral-400 hover:border-primary hover:text-primary dark:hover:border-primary-400 dark:hover:text-primary-400 transition-colors"
        @click="suggestTool"
      >
        <i class="fa fa-lightbulb mr-2"></i>提交工具建议
      </button>
    </div>
  </main>
</template>
<script>
import ToolCard from '@/components/ToolCard.vue'
import { tools, CATEGORIES, getCategoryColor } from '@/config/tools'

export default {
  name: 'Home',
  components: { ToolCard },
  data() {
    return {
      searchQuery: '',
      activeCategory: CATEGORIES.ALL,
      categories: Object.values(CATEGORIES),
      tools,
      CATEGORIES
    };
  },
  computed: {
    filteredTools() {
      return this.tools.filter(tool => {
        // 搜索过滤
        const matchesSearch = this.searchQuery === '' || 
          tool.title.toLowerCase().includes(this.searchQuery.toLowerCase()) || 
          tool.desc.toLowerCase().includes(this.searchQuery.toLowerCase());
        
        // 分类过滤
        const matchesCategory = this.activeCategory === CATEGORIES.ALL || 
          tool.category === this.activeCategory;
        
        return matchesSearch && matchesCategory;
      });
    },
    categoryColors() {
      return {
        [CATEGORIES.ALL]: getCategoryColor(),
        [CATEGORIES.DEV]: getCategoryColor(CATEGORIES.DEV),
        [CATEGORIES.DESIGN]: getCategoryColor(CATEGORIES.DESIGN),
        [CATEGORIES.TEXT]: getCategoryColor(CATEGORIES.TEXT),
        [CATEGORIES.PDF]: getCategoryColor(CATEGORIES.PDF)
      };
    }
  },
  methods: {
    setCategory(category) {
      this.activeCategory = category;
    },
    getCategoryCount(category) {
      return this.tools.filter(tool => tool.category === category).length;
    },
    getCategoryClasses(category) {
      const colors = this.categoryColors[category];
      return this.activeCategory === category
        ? `${colors.bg} text-white ${colors.bgDark} shadow-md`
        : `bg-neutral-100 dark:bg-neutral-800 text-neutral-600 dark:text-neutral-300 hover:bg-neutral-200 dark:hover:bg-neutral-700`;
    },
    resetFilters() {
      this.searchQuery = '';
      this.activeCategory = CATEGORIES.ALL;
    },
    suggestTool() {
      // 这里可以添加建议工具的逻辑
      alert('感谢您的建议！该功能正在开发中...');
    }
  }
};
</script>
