// 根据分类获取对应颜色配置
export const getCategoryColor = (category) => {
  switch (category) {
    case CATEGORIES.DEV:
      return {
        bg: 'bg-blue-500',
        bgDark: 'dark:bg-blue-600',
        text: 'text-blue-600',
        textDark: 'dark:text-blue-400',
        hover: 'hover:text-blue-700',
        hoverDark: 'dark:hover:text-blue-300',
        light: 'bg-blue-50',
        lightDark: 'dark:bg-blue-900/30'
      };
    case CATEGORIES.DESIGN:
      return {
        bg: 'bg-purple-500',
        bgDark: 'dark:bg-purple-600',
        text: 'text-purple-600',
        textDark: 'dark:text-purple-400',
        hover: 'hover:text-purple-700',
        hoverDark: 'dark:hover:text-purple-300',
        light: 'bg-purple-50',
        lightDark: 'dark:bg-purple-900/30'
      };
    case CATEGORIES.TEXT:
      return {
        bg: 'bg-green-500',
        bgDark: 'dark:bg-green-600',
        text: 'text-green-600',
        textDark: 'dark:text-green-400',
        hover: 'hover:text-green-700',
        hoverDark: 'dark:hover:text-green-300',
        light: 'bg-green-50',
        lightDark: 'dark:bg-green-900/30'
      };
    case CATEGORIES.PDF:
      return {
        bg: 'bg-red-500',
        bgDark: 'dark:bg-red-600',
        text: 'text-red-600',
        textDark: 'dark:text-red-400',
        hover: 'hover:text-red-700',
        hoverDark: 'dark:hover:text-red-300',
        light: 'bg-red-50',
        lightDark: 'dark:bg-red-900/30'
      };
    default:
      return {
        bg: 'bg-primary',
        bgDark: 'dark:bg-yellow-500',
        text: 'text-primary',
        textDark: 'dark:text-primary-300',
        hover: 'hover:text-secondary',
        hoverDark: 'dark:hover:text-yellow-400',
        light: 'bg-primary bg-opacity-10',
        lightDark: 'dark:bg-primary-800 dark:bg-opacity-20'
      };
  }
};

export default {
  tools,
  CATEGORIES,
  getCategoryColor
}; 