<template>
  <div class="card shadow-custom p-6 md:p-6 bg-white dark:bg-neutral-800 rounded-xl">
    <div class="flex justify-between items-center mb-6">
      <div class="flex items-center space-x-3">
        <button class="flex items-center text-neutral-400 hover:text-primary transition-all-300 font-medium"
          @click="$emit('show-help')" title="帮助">
          <i class="fa fa-question-circle text-xl"></i>
          <span class="hidden md:inline ml-1">帮助</span>
        </button>
        <span class="border-l border-primary-100 dark:border-primary-800 h-6 mx-3"></span>
        <h2 class="text-xl font-bold flex items-center text-primary">
          <i class="fa fa-eye mr-2"></i>雪碧图预览
        </h2>
      </div>
      <div class="flex space-x-2">
        <button
          class="p-2 rounded-lg border border-primary-100 dark:border-primary-800 hover:bg-primary/10 transition-all-300"
          @click="zoomIn">
          <i class="fa fa-search-plus text-primary"></i>
        </button>
        <button
          class="p-2 rounded-lg border border-primary-100 dark:border-primary-800 hover:bg-primary/10 transition-all-300"
          @click="zoomOut">
          <i class="fa fa-search-minus text-primary"></i>
        </button>
        <button
          class="p-2 rounded-lg border border-primary-100 dark:border-primary-800 hover:bg-primary/10 transition-all-300"
          @click="resetZoom">
          <i class="fa fa-expand text-primary"></i>
        </button>
      </div>
    </div>
    <div class="sprite-preview-container rounded-xl overflow-auto flex items-center justify-center bg-bg-light dark:bg-neutral-900" style="min-height:220px;">
      <div class="relative transition-all duration-300" :style="{ transform: `scale(${currentScale})` }">
        <template v-if="!hasSprite">
          <div class="flex flex-col items-center justify-center w-full h-full py-10">
            <i class="fa fa-picture-o text-5xl mb-4 text-primary dark:text-primary-400"></i>
            <p class="text-lg text-primary/80 dark:text-primary-300/80 font-medium">请上传图标，生成的雪碧图将在此预览</p>
          </div>
        </template>
        <template v-else>
          <div v-if="isDark" class="absolute inset-0 bg-white/80 rounded-lg pointer-events-none z-10"></div>
          <div v-html="previewHtml" :class="{ 'relative z-20': isDark }"></div>
        </template>
      </div>
    </div>
    <div class="mt-6 grid grid-cols-2 gap-4">
      <button class="btn-primary py-3 rounded-lg flex items-center justify-center disabled:btn-disabled"
        :disabled="files.length < 2" @click="generateSprite">
        <i class="fa fa-magic mr-2"></i>生成雪碧图
      </button>
      <button class="btn-secondary py-3 rounded-lg flex items-center justify-center disabled:btn-disabled"
        :disabled="!spriteBlob" @click="downloadSprite">
        <i class="fa fa-download mr-2"></i>下载图片
      </button>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    files: { type: Array, default: () => [] },
    customNames: { type: Object, default: () => ({}) },
    settings: { type: Object, required: true }
  },
  data() {
    return {
      currentScale: 1,
      previewHtml: `<div class="text-center text-neutral-400">
        <i class="fa fa-picture-o text-5xl mb-3"></i>
        <p>上传图标后生成的雪碧图将显示在这里</p>
      </div>`,
      spriteBlob: null,
      css: '',
      debugInfo: []
    };
  },
  computed: {
    isDark() {
      return document.documentElement.classList.contains('dark');
    },
    hasSprite() {
      return this.spriteBlob !== null;
    }
  },
  methods: {
    zoomIn() { if (this.currentScale < 2) this.currentScale += 0.2; },
    zoomOut() { if (this.currentScale > 0.5) this.currentScale -= 0.2; },
    resetZoom() { this.currentScale = 1; },
    async generateSprite() {
      if (this.files.length < 2) return;
      this.previewHtml = `<div class="text-center text-neutral-400 py-10">
        <div class="inline-block animate-spin rounded-full h-8 w-8 border-b-2 border-primary mb-3"></div>
        <p>正在生成雪碧图...</p>
      </div>`;
      this.spriteBlob = null;
      this.css = '';
      this.debugInfo = [];
      // 加载图片
      const images = await Promise.all(this.files.map(file => {
        return new Promise((resolve, reject) => {
          const img = new window.Image();
          img.onload = () => resolve({ img, file });
          img.onerror = reject;
          img.src = URL.createObjectURL(file);
        });
      }));
      // 图标大小调整
      let processed = [];
      const resizeMode = this.settings.resizeMode;
      if (resizeMode !== 'none') {
        const tempCanvas = document.createElement('canvas');
        const tempCtx = tempCanvas.getContext('2d');
        for (const item of images) {
          let w = item.img.width, h = item.img.height;
          if (resizeMode === 'fixed') {
            w = this.settings.fixedWidth;
            h = this.settings.fixedHeight;
          } else if (resizeMode === 'max') {
            const mw = this.settings.maxWidth, mh = this.settings.maxHeight;
            if (w > mw || h > mh) {
              const ratio = Math.min(mw / w, mh / h);
              w = Math.floor(w * ratio);
              h = Math.floor(h * ratio);
            }
          } else if (resizeMode === 'percentage') {
            const p = this.settings.scalePercentage;
            w = Math.floor(w * p / 100);
            h = Math.floor(h * p / 100);
          }
          tempCanvas.width = w;
          tempCanvas.height = h;
          tempCtx.clearRect(0, 0, w, h);
          tempCtx.drawImage(item.img, 0, 0, w, h);
          const newImg = new window.Image();
          newImg.src = tempCanvas.toDataURL();
          await new Promise(res => { newImg.onload = res; });
          processed.push({ img: newImg, file: item.file, width: w, height: h });
        }
      } else {
        processed = images.map(item => ({
          img: item.img, file: item.file, width: item.img.width, height: item.img.height
        }));
      }
      // 雪碧图布局
      const spacing = this.settings.spacing;
      let width = 0, height = 0, positions = [];
      if (this.settings.layoutType === 'horizontal') {
        width = spacing * (processed.length - 1);
        height = 0;
        processed.forEach(item => {
          width += item.width;
          height = Math.max(height, item.height);
        });
        let x = 0;
        positions = processed.map(item => {
          const pos = { x, y: 0, ...item };
          x += item.width + spacing;
          return pos;
        });
      } else if (this.settings.layoutType === 'vertical') {
        width = 0;
        height = spacing * (processed.length - 1);
        processed.forEach(item => {
          height += item.height;
          width = Math.max(width, item.width);
        });
        let y = 0;
        positions = processed.map(item => {
          const pos = { x: 0, y, ...item };
          y += item.height + spacing;
          return pos;
        });
      } else {
        // binary-tree packing
        class Node {
          constructor(x, y, w, h) {
            this.x = x; this.y = y; this.w = w; this.h = h;
            this.used = false; this.right = null; this.down = null;
          }
          fit(w, h) {
            if (this.used) {
              return this.right && this.right.fit(w, h) || this.down && this.down.fit(w, h);
            } else if (w <= this.w && h <= this.h) {
              this.used = true;
              this.right = new Node(this.x + w + spacing, this.y, this.w - w - spacing, h);
              this.down = new Node(this.x, this.y + h + spacing, this.w, this.h - h - spacing);
              return this;
            }
            return null;
          }
        }
        // 按面积降序
        processed.sort((a, b) => b.width * b.height - a.width * a.height);
        let totalArea = 0, maxW = 0, maxH = 0;
        processed.forEach(item => {
          totalArea += (item.width + spacing) * (item.height + spacing);
          maxW = Math.max(maxW, item.width);
          maxH = Math.max(maxH, item.height);
        });
        let best = null;
        for (const scale of [1.1, 1.2, 1.3, 1.4, 1.5]) {
          const iw = Math.max(maxW, Math.ceil(Math.sqrt(totalArea) * scale));
          const ih = Math.max(maxH, Math.ceil(Math.sqrt(totalArea) * scale));
          let root = new Node(0, 0, iw, ih);
          let ok = true, pos = [], maxRight = 0, maxBottom = 0;
          for (let i = 0; i < processed.length; i++) {
            const item = processed[i];
            const node = root.fit(item.width, item.height);
            if (node) {
              pos.push({ x: node.x, y: node.y, ...item });
              maxRight = Math.max(maxRight, node.x + item.width);
              maxBottom = Math.max(maxBottom, node.y + item.height);
            } else {
              ok = false; break;
            }
          }
          if (ok) {
            const eff = totalArea / (maxRight * maxBottom);
            if (!best || eff > best.eff) best = { pos, width: maxRight, height: maxBottom, eff };
            if (eff > 0.8) break;
          }
        }
        if (best) {
          width = best.width;
          height = best.height;
          positions = best.pos;
        } else {
          // fallback: grid
          const cols = Math.ceil(Math.sqrt(processed.length));
          const rows = Math.ceil(processed.length / cols);
          let maxW = 0, maxH = 0;
          processed.forEach(item => {
            maxW = Math.max(maxW, item.width);
            maxH = Math.max(maxH, item.height);
          });
          width = cols * maxW + (cols - 1) * spacing;
          height = rows * maxH + (rows - 1) * spacing;
          positions = processed.map((item, idx) => {
            const row = Math.floor(idx / cols), col = idx % cols;
            return { x: col * (maxW + spacing), y: row * (maxH + spacing), ...item };
          });
        }
      }
      // 绘制canvas
      const canvas = document.createElement('canvas');
      canvas.width = width;
      canvas.height = height;
      const ctx = canvas.getContext('2d');
      ctx.clearRect(0, 0, width, height);
      positions.forEach(pos => {
        ctx.drawImage(pos.img, pos.x, pos.y, pos.width, pos.height);
      });
      // 生成CSS
      let css = `.sprite {\n  background-image: url('sprite.png');\n  background-repeat: no-repeat;\n  display: inline-block;\n}\n\n`;
      positions.forEach((pos, idx) => {
        let className = this.customNames[pos.file.name] ||
          pos.file.name.replace(/\.[^/.]+$/, '').replace(/[^a-zA-Z0-9]/g, '-').replace(/^-+|-+$/g, '');
        if (!className || className === '-' || className === '') className = `icon-${idx + 1}`;
        css += `.sprite-${className} {\n`;
        css += `  width: ${pos.width}px;\n`;
        css += `  height: ${pos.height}px;\n`;
        css += `  background-position: -${pos.x}px -${pos.y}px;\n`;
        css += `}\n\n`;
      });
      this.css = css;
      this.$emit('sprite-generated', { css });
      // 输出图片
      const format = this.settings.outputFormat;
      const quality = this.settings.quality;
      canvas.toBlob(blob => {
        this.spriteBlob = blob;
        const imgUrl = URL.createObjectURL(blob);
        let previewHtml = `
          <img src="${imgUrl}" alt="Sprite Preview" class="max-w-full border border-primary-100 dark:border-primary-800 rounded-lg shadow-sm">
          <div class="absolute top-2 right-2 bg-white/80 px-2 py-1 rounded text-xs font-medium text-neutral-700">
            ${canvas.width} × ${canvas.height} px
          </div>
        `;
        if (this.settings.enableDebug && this.debugInfo.length > 0) {
          previewHtml += `
            <div class="mt-4 p-3 bg-neutral-100 rounded-lg text-xs text-neutral-700 max-h-40 overflow-y-auto">
              <h4 class="font-medium mb-1">调试信息:</h4>
              <ul class="list-disc list-inside">
                ${this.debugInfo.map(info => `<li>${info}</li>`).join('')}
              </ul>
            </div>
          `;
        }
        this.previewHtml = previewHtml;
      }, format === 'png' ? 'image/png' : 'image/jpeg', quality);
    },
    downloadSprite() {
      if (!this.spriteBlob) return;
      const link = document.createElement('a');
      link.href = URL.createObjectURL(this.spriteBlob);
      link.download = 'sprite.' + this.settings.outputFormat;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  }
};
</script>
