<template>
  <div class="p-6 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 rounded-xl">
    <h2 class="text-lg font-semibold mb-4 flex items-center text-primary dark:text-primary-400">
      <i class="fa fa-sliders mr-2"></i>雪碧图设置
    </h2>
    <div class="space-y-4">
      <div>
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200 mb-1">排列方式</label>
        <select v-model="local.layoutType" class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          <option value="horizontal">水平排列</option>
          <option value="vertical">垂直排列</option>
          <option value="binary-tree">最优排列</option>
        </select>
      </div>
      <div>
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200 mb-1">图标间距 (px)</label>
        <input v-model.number="local.spacing" type="number" min="0" max="50"
          class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
      </div>
      <div>
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200 mb-1">图标大小调整</label>
        <select v-model="local.resizeMode" class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          <option value="none">保持原始大小</option>
          <option value="fixed">固定大小</option>
          <option value="max">限制最大尺寸</option>
          <option value="percentage">按比例缩放</option>
        </select>
        <div v-if="local.resizeMode==='fixed'" class="mt-2 flex space-x-2">
          <div class="flex-1">
            <label class="block text-xs text-neutral-600 dark:text-neutral-400 mb-1">宽度 (px)</label>
            <input v-model.number="local.fixedWidth" type="number" min="1" max="1000"
              class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          </div>
          <div class="flex-1">
            <label class="block text-xs text-neutral-600 dark:text-neutral-400 mb-1">高度 (px)</label>
            <input v-model.number="local.fixedHeight" type="number" min="1" max="1000"
              class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          </div>
        </div>
        <div v-if="local.resizeMode==='max'" class="mt-2 flex space-x-2">
          <div class="flex-1">
            <label class="block text-xs text-neutral-600 dark:text-neutral-400 mb-1">最大宽度 (px)</label>
            <input v-model.number="local.maxWidth" type="number" min="1" max="1000"
              class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          </div>
          <div class="flex-1">
            <label class="block text-xs text-neutral-600 dark:text-neutral-400 mb-1">最大高度 (px)</label>
            <input v-model.number="local.maxHeight" type="number" min="1" max="1000"
              class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          </div>
        </div>
        <div v-if="local.resizeMode==='percentage'" class="mt-2">
          <label class="block text-xs text-neutral-600 dark:text-neutral-400 mb-1">缩放比例 (%)</label>
          <input v-model.number="local.scalePercentage" type="number" min="1" max="200"
            class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
        </div>
      </div>
      <div>
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200 mb-1">输出格式</label>
        <select v-model="local.outputFormat" class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          <option value="png">PNG</option>
          <option value="jpg">JPG</option>
        </select>
      </div>
      <div v-if="local.outputFormat==='jpg'">
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200 mb-1">图片质量 (仅JPG)</label>
        <input v-model.number="local.quality" type="range" min="0.1" max="1" step="0.1" class="w-full">
        <div class="flex justify-between text-xs text-neutral-500 dark:text-neutral-400 mt-1">
          <span>低</span>
          <span>{{ local.quality }}</span>
          <span>高</span>
        </div>
      </div>
      <div>
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200 mb-1">CSS格式</label>
        <select v-model="local.cssFormat" class="w-full px-3 py-2 border border-primary-100 dark:border-primary-800 bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-100 rounded-lg focus:outline-none focus:ring-2 focus:ring-primary/50 focus:border-primary">
          <option value="css">CSS</option>
          <option value="less">Less</option>
          <option value="scss">SCSS</option>
        </select>
      </div>
      <div class="flex items-center">
        <input v-model="local.enableDebug" type="checkbox" class="h-4 w-4 text-primary focus:ring-primary border-primary-100 dark:border-primary-800 rounded">
        <label class="ml-2 block text-sm text-neutral-700 dark:text-neutral-200">启用调试模式</label>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    value: { type: Object, required: true }
  },
  computed: {
    local: {
      get() { return this.value; },
      set(val) { this.$emit('input', val); }
    }
  }
};
</script>
