import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/Home.vue';
import SpriteGeneratorPage from '@/views/SpriteGeneratorPage.vue';
import HelpPage from '@/views/HelpPage.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: { title: '创新工具箱' }
    },
    {
      path: '/sprite-generator',
      name: 'SpriteGenerator',
      component: SpriteGeneratorPage,
      meta: { title: '雪碧图生成器' }
    },
    {
      path: '/help',
      name: 'Help',
      component: HelpPage,
      meta: { title: '帮助' }
    },
    {
      path: '/markdown-editor',
      name: 'MarkdownEditor',
      component: () => import('@/views/MarkdownEditor.vue'),
      meta: { title: 'Markdown 编辑器' }
    },
    {
      path: '/base64-image-encoder',
      name: 'Base64ImageEncoder',
      component: () => import('@/views/Base64ImageEncoder.vue'),
      meta: { title: 'Base64 图片编码器' }
    },
    {
      path: '/css-gradient-generator',
      name: 'CssGradientGenerator',
      component: () => import('@/views/CssGradientGenerator.vue'),
      meta: { title: 'CSS 渐变生成器' }
    },
    {
      path: '/css2tailwind',
      name: 'Css2Tailwind',
      component: () => import('@/views/Css2TailwindPage.vue'),
      meta: { title: 'CSS转Tailwind' }
    },
    {
      path: '/text-crypto',
      name: 'TextCrypto',
      component: () => import('@/views/TextCryptoPage.vue'),
      meta: { title: '文本加解密' }
    },
    {
      path: '/pdf-tools',
      name: 'PdfTools',
      component: () => import('@/views/PdfTools.vue'),
      meta: { title: 'PDF工具箱' }
    }
  ]
});
