<template>
  <div v-if="show" class="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black bg-opacity-50">
    <div class="bg-white dark:bg-neutral-800 rounded-xl shadow-2xl max-w-2xl w-full max-h-[80vh] flex flex-col">
      <div class="flex justify-between items-center p-4 border-b border-neutral-200 dark:border-neutral-700">
        <h2 class="text-xl font-bold text-primary dark:text-primary-400">
          {{ title }}
        </h2>
        <button 
          @click="close" 
          class="text-neutral-500 hover:text-neutral-700 dark:text-neutral-400 dark:hover:text-neutral-200"
        >
          <i class="fa fa-times text-xl"></i>
        </button>
      </div>
      <div class="p-6 overflow-y-auto flex-grow">
        <slot></slot>
      </div>
      <div class="p-4 border-t border-neutral-200 dark:border-neutral-700 flex justify-end">
        <button 
          @click="close" 
          class="px-4 py-2 rounded bg-primary text-white font-semibold hover:bg-primary/80 transition"
        >
          关闭
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelpDialog',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '使用说明'
    }
  },
  methods: {
    close() {
      this.$emit('close');
    }
  }
};
</script>
