<template>
  <div class="card shadow-custom p-6 md:p-6 bg-white dark:bg-neutral-800 rounded-xl">
    <div class="flex justify-between items-center mb-6">
      <h2 class="text-xl font-bold flex items-center text-primary">
        <i class="fa fa-code mr-2"></i>CSS代码
      </h2>
      <button
        class="text-sm px-4 py-2 bg-neutral-100 text-primary rounded-lg hover:bg-primary/10 transition-all-300 disabled:opacity-50 disabled:cursor-not-allowed"
        :disabled="!css" @click="$emit('copy')">
        <i class="fa fa-copy mr-1"></i>复制代码
      </button>
    </div>
    <div class="overflow-hidden">
      <div class="p-4 bg-neutral-800 dark:bg-neutral-900 text-neutral-100 font-mono text-base h-60 overflow-auto rounded-xl">
        <pre class="whitespace-pre-wrap">{{ displayCSS }}</pre>
      </div>
    </div>
    <div class="mt-6">
      <button
        class="w-full bg-neutral-700 text-white py-3 rounded-lg flex items-center justify-center hover:bg-neutral-800 transition-all-300 disabled:opacity-50 disabled:cursor-not-allowed"
        :disabled="!css" @click="$emit('download')">
        <i class="fa fa-download mr-2"></i>下载CSS文件
      </button>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    css: { type: String, default: '' },
    format: { type: String, default: 'css' }
  },
  computed: {
    displayCSS() {
      if (!this.css) return '/* 生成的CSS代码将显示在这里 */';
      if (this.format === 'css') return this.css;
      let lines = this.css.split('\n');
      let newLines = [];
      lines.forEach(line => {
        if (line.trim().startsWith('.sprite-')) newLines.push(line.trim());
        else if (line.trim().startsWith('}')) newLines.push(line);
        else if (line.trim()) newLines.push('  ' + line);
        else newLines.push(line);
      });
      if (this.format === 'less') {
        newLines.unshift('.sprite() {');
        newLines.push('}');
      } else {
        newLines.unshift('.sprite {');
        newLines.push('}');
      }
      return newLines.join('\n');
    }
  }
};
</script>
