<template>
  <div
    class="group relative bg-white dark:bg-neutral-800 rounded-2xl shadow-custom p-0 flex flex-col items-center transition-all duration-300 w-full overflow-hidden hover:-translate-y-1 hover:shadow-2xl"
    style="box-shadow: 0 8px 30px 0 rgba(16,30,54,0.08);"
  >
    <!-- 顶部彩色标签 - 根据分类变色 -->
    <div 
      :class="[
        getColorStyle('top-bar'),
        'absolute top-0 left-0 w-full h-1'
      ]"
    ></div>
    
    <!-- 内容 -->
    <div class="relative z-10 flex flex-col items-start w-full px-5 py-6 h-full">
      <div class="flex items-center mb-4 w-full justify-between">
        <div class="flex items-center">
          <div 
            :class="[
              getColorStyle('icon-bg'),
              'w-10 h-10 flex items-center justify-center rounded-lg mr-3 shadow-md'
            ]"
          >
            <i 
              :class="[
                tool.icon,
                getColorStyle('icon'),
                'text-lg'
              ]"
            ></i>
          </div>
          <h2 class="text-lg font-bold text-neutral-900 dark:text-white group-hover:text-primary dark:group-hover:text-yellow-400 transition-all duration-300 tracking-tight leading-tight">
            {{ tool.title }}
          </h2>
        </div>
        
        <!-- 分类标签移到右上角 -->
        <div 
          :class="[
            getColorStyle('tag-bg'),
            getColorStyle('tag-text'),
            'text-xs font-medium px-2 py-1 rounded-full'
          ]"
        >
          {{ tool.category }}
        </div>
      </div>
      
      <p class="text-neutral-600 dark:text-neutral-300 leading-relaxed text-sm mb-4 line-clamp-2">
        {{ tool.desc }}
      </p>
      
      <div class="mt-auto flex items-center w-full justify-between">
        <router-link
          :to="tool.route"
          :class="[
            getColorStyle('link'),
            getColorStyle('link-hover'),
            'inline-flex items-center font-medium transition-all duration-300'
          ]"
        >
          <span>进入工具</span>
          <i class="fa fa-arrow-right ml-2 text-sm"></i>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { getCategoryColor, CATEGORIES } from '@/config/tools';

export default {
  name: 'ToolCard',
  props: {
    tool: { type: Object, required: true }
  },
  methods: {
    getColorStyle(element) {
      const colors = getCategoryColor(this.tool.category);
      
      switch (element) {
        case 'top-bar':
          return this.tool.category === CATEGORIES.DEV ? 'bg-gradient-to-r from-blue-500 to-blue-400' :
                 this.tool.category === CATEGORIES.DESIGN ? 'bg-gradient-to-r from-purple-500 to-purple-400' :
                 this.tool.category === CATEGORIES.TEXT ? 'bg-gradient-to-r from-green-500 to-green-400' :
                 this.tool.category === CATEGORIES.PDF ? 'bg-gradient-to-r from-red-500 to-red-400' :
                 'bg-gradient-to-r from-primary to-secondary';
        case 'icon-bg':
          return colors.light + ' ' + colors.lightDark;
        case 'icon':
          return colors.text + ' ' + colors.textDark;
        case 'tag-bg':
          return colors.light + ' ' + colors.lightDark;
        case 'tag-text':
          return colors.text + ' ' + colors.textDark;
        case 'link':
          return colors.text + ' ' + colors.textDark;
        case 'link-hover':
          return colors.hover + ' ' + colors.hoverDark;
        default:
          return '';
      }
    }
  }
};
</script>
